import { oldTree, vm } from './render'
import {diff } from './diff'
import {patch} from './patch'

let save = {}
let newTree

export function useState(state) {
  if (Object.keys(save).length > 0) {
    state = save
  }

  let proxy = new Proxy(state, {
    get(obj, key) {
      if (save[key]) {
        return save[key]
      } else {
        return obj[key]
      }
    },
    set(obj, key, val) {
      obj[key] = val
      save[key] = val
      newTree = vm.type()
      let patches = diff(oldTree,newTree)
      patch(el,patches)
      return true
    }
  })

  return proxy
}
