import { useState } from './src/hooks'
import { html as h } from './src/h'
import { mount } from './src/render'

function counter() {
  const state = useState({
    count: 0
  })

  return h`
    <div>
      <p>${state.count}</p>
      <button onclick=${() => {
        state.count++
      }}>+</button>
    </div> 
  `
}

mount(h`<${counter} />`, document.body)
